.contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px 0;

  &__form {
    display: grid;
    grid-template-columns: auto repeat(2, max-content);
    gap: 30px;
    width: 100%;
  }

  &__input {
    display: block;
    grid-column: 1/4;
    width: 100%;
    padding: 17px 0;
    font-size: 20px;
    line-height: 26px;
    color: $amethystSmoke;
    border-bottom: 1px solid $amethystSmoke;
  }

  &__input:focus {
    color: $black-text;
  }

  &__submit {
    grid-area: 4/1/5/2;
  }

  &__file-btn {
    display: inline-block;
    grid-row: 4/5;
    width: 30px;
    height: 30px;
    background-color: transparent;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    transition: transform 0.3s;
  }

  &__file-btn:hover {
    transition: transform 0.3s;
    transform: scale(1.2);
  }

  &__paper-clip {
    background-image: url("../images/paperClip.webp");
  }

  &__arrow {
    background-image: url("../images/arrow-right.webp");
  }
}

@media screen and (max-width: 400px) {
  .contact {
    &__paper-clip {
      grid-area: 4/2/5/3;
    }

    &__arrow {
      grid-area: 4/3/5/4;
    }

    &__submit {
      grid-area: 5/1/6/2;
    }
  }
}
