.portfolio {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px 0;

  &__wrapper {
    position: relative;
  }

  &__link-more {
    position: absolute;
    top: 80px;
    right: 0;
    display: flex;
    width: max-content;
    padding-right: 34px;
    font-size: 24px;
    font-weight: 500;
    line-height: 32px;
    text-decoration-line: underline;
    color: $black-text;
    background-image: url("../images/arrow-right.webp");
    background-repeat: no-repeat;
    background-position: right center;
  }

  &__items {
    display: grid;
    grid-template-rows: max-content;
    grid-template-columns: repeat (2, 1fr );
    gap: 30px;
  }

  &__item {
    display: flex;
    grid-row: 1/2;
    width: 100%;
    height: 29.6vw;
    box-shadow: 0 0 25px 0 rgba(34, 60, 80, 0.35);
  }

  &__item:nth-child(1) {
    background: url("../images/portfolio/portfolio1.webp") no-repeat center;
  }

  &__item:nth-child(2) {
    background: url("../images/portfolio/portfolio2.webp") no-repeat center;
  }
}

@media screen and (max-width: 800px) {
  .portfolio {
    &__items {
      display: flex;
      align-items: center;
      justify-content: center;
      padding-bottom: 60px;
    }

    &__item {
      max-width: 400px;
      height: 50vh;
    }

    &__item:nth-child(2) {
      display: none;
    }

    &__link-more {
      top: auto;
      bottom: 0;
      height: 32px;
    }
  }
}
