.blog {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px 0;

  &__wrapper {
    position: relative;
    height: 350px;
    overflow: hidden;
  }

  &__items-article {
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    justify-content: space-between;
    width: 82.9%;
  }

  &__item-article {
    display: flex;
    flex-direction: column;
    justify-content: end;
    width: 27.71%;
    height: 350px;
    transition: 0.3s;
  }

  &__item-article:nth-child(3n+1) {
    background: linear-gradient(180deg, rgba(196, 196, 196, 0) 0%, $black-text 100%), url("../images/blog/blog1.webp");
    background-repeat: no-repeat;
    background-position: center top;
    background-size: cover;
  }

  &__item-article:nth-child(3n+2) {
    background: linear-gradient(180deg, rgba(196, 196, 196, 0) 0%, $black-text 100%), url("../images/blog/blog2.webp");
    background-repeat: no-repeat;
    background-position: center top;
    background-size: cover;
  }

  &__item-article:nth-child(3n+3) {
    background: linear-gradient(180deg, rgba(196, 196, 196, 0) 0%, $black-text 100%), url("../images/blog/blog3.webp");
    background-repeat: no-repeat;
    background-position: center top;
    background-size: cover;
  }

  &__item-article_active {
    width: 38.5%;
    background: cover;
    transition: 0.3s;
  }

  &__date {
    padding: 20px 30px 10px;
    font-size: 16px;
    font-weight: 500;
    line-height: 21px;
    color: $white;

    &_active {
      color: $purple;
      background-color: $white;
    }
  }

  &__item-title {
    padding: 0 20px 30px;
    font-size: 16px;
    font-weight: 700;
    line-height: 24px;
    color: $white;

    &_active {
      color: $black-text;
      background-color: $white;
    }
  }
}

@media screen and (max-width: 880px) {
  .blog {
    &__wrapper {
      height: initial;
    }

    &__items-article {
      position: relative;
      width: 100%;
    }
  }
}

@media screen and (max-width: 780px) {
  .blog {
    &__wrapper {
      padding-bottom: 60px;
    }

    &__item-article {
      display: flex;
      flex-shrink: 0;
      box-shadow: 1px 1px 8px 0 rgba(34, 60, 80, 0.32);
    }
  }
}
