.clients {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px 0;

  // &__wrapper {

  // }

  &__items {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  &__item {
    display: flex;
    width: 170px;
    height: 170px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 10px;
    box-shadow: 1px 0 8px 0 rgba(34, 60, 80, 0.1);
  }

  &__item:nth-child(4n+1) {
    background-image: url("../images/clients/clients1.webp");
  }

  &__item:nth-child(4n+2) {
    background-image: url("../images/clients/clients2.webp");
  }

  &__item:nth-child(4n+3) {
    background-image: url("../images/clients/clients3.webp");
  }

  &__item:nth-child(4n+4) {
    background-image: url("../images/clients/clients4.webp");
  }

  &__item:nth-child(5) {
    background-image: url("../images/clients/clients5.webp");
  }
}

@media screen and (max-width: 1050px) {
  .clients {
    &__swiper {
      position: relative;
      display: flex;
      padding-bottom: 50px;
      overflow: hidden;
    }

    &__items {
      position: relative;
      display: flex;
      width: 100%;
    }

    &__item {
      display: flex;
      flex-shrink: 0;
      height: calc((94vw - (20px * 3)) / 4);
    }
  }
}

@media screen and (max-width: 800px) {
  .clients {
    &__item {
      height: calc((94vw - (20px * 2)) / 3);
    }
  }
}

@media screen and (max-width: 550px) {
  .clients {
    &__item {
      height: calc((94vw - 20px) / 2);
    }
  }
}

@media screen and (max-width: 400px) {
  .clients {
    &__item {
      height: 94vw;
    }
  }
}
