.testimonials {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px 0;

  &__wrapper {
    display: flex;
    flex-direction: column;
  }

  &__slider {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    background-image: url("../images/testimonials/doubleQuote.webp");
    background-repeat: no-repeat;
    background-position-x: center;
    background-position-y: 35px;
  }

  &__items {
    position: relative;
    display: flex;
    width: 100%;
    height: 500px;
  }

  &__item {
    position: absolute;
    display: flex;
    width: 70px;
    height: 70px;
    padding-top: 70px;
    overflow: hidden;
    filter: blur(2px);
    background-repeat: no-repeat;
    background-position-x: center;
    background-position-y: top;
    background-size: 70px;
    border-radius: 10px;
    opacity: 0;

    &_active {
      display: grid;
      grid-template-rows: 130px 26px 26px;
      gap: 5px;
      width: 130px;
      height: max-content;
      padding-top: 0;
      text-align: center;
      filter: blur(0);
      background-size: 120px;
      transition: 0.3s;
    }
  }

  &__item:nth-child(5n+1) {
    background-image: url("../images/testimonials/image1.webp");
  }

  &__item:nth-child(5n+2) {
    background-image: url("../images/testimonials/image2.webp");
  }

  &__item:nth-child(5n+3) {
    background-image: url("../images/testimonials/image3.webp");
  }

  &__item:nth-child(5n+4) {
    background-image: url("../images/testimonials/image4.webp");
  }

  &__item:nth-child(5n+5) {
    background-image: url("../images/testimonials/image5.webp");
  }

  &__autor {
    grid-area: 2/1/3/2;
    font-size: 20px;
    font-weight: 500;
    line-height: 26px;
  }

  &__post {
    grid-area: 3/1/4/2;
    font-size: 15px;
    line-height: 21px;
  }

  &__btn {
    position: absolute;
    display: inline-block;
    padding: 10px 22px;
    font-size: 18px;
    line-height: 32px;
    color: $black-text;
    background-color: transparent;
    border: 1px solid $black-text;
    border-radius: 5px;
  }

  &__btn:hover {
    box-shadow: 4px 4px 8px 0 rgba(34, 60, 80, 0.2);
    transform: scale(1.02);
  }

  &__prev {
    bottom: 0;
    left: 35%;
  }

  &__next {
    right: 35%;
    bottom: 0;
  }

  &__text {
    position: absolute;
    top: 100px;
    left: 25%;
    width: 50%;
    height: 100px;
    overflow: hidden;
    text-align: center;
  }
}

@media screen and (max-width: 620px) {
  .testimonials {
    &__slider {
      background-position-y: 0;
    }

    &__items {
      height: 450px;
    }

    &__item {
      bottom: 0;
      filter: blur(0);
      background-size: cover;
      opacity: 1;

      &_active {
        bottom: 100px;
        width: 200px;
        height: max-content;
        background-size: 130px;
        transition: 0ms;
      }
    }

    &__btn {
      width: 70px;
      height: 70px;
      background-color: transparent;
      border: none;
    }

    &__prev {
      bottom: 0;
      left: 0;
    }

    &__next {
      right: 0;
      bottom: 0;
    }

    &__text {
      top: 50px;
      left: 10%;
      width: 80%;
    }
  }
}
