.header {
  display: flex;
  justify-content: center;
  width: 100%;
  padding-top: 50px;

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: start;
    width: 100%;
    height: max-content;
  }

  &__logo {
    display: inline-block;
    width: 40px;
    height: 40px;
    margin-right: 100px;
    background: url("../images/logo.svg") no-repeat center;
  }

  &__menu-items {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 338px;
  }

  &__item-link {
    font-family: "Red Hat Display", sans-serif;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 26px;
    color: $gray-text;
    transition: color 0.3s;

    &_active {
      color: $black-text;
    }
  }

  &__item-link:hover {
    color: $black-text;
    transition: color 0.3s;
  }
}

@media screen and (max-width: 800px) {
  .header {
    position: relative;

    &__wrapper {
      align-items: center;
      justify-content: center;
      height: 100%;
    }

    &__logo {
      position: fixed;
      top: 20px;
      left: 3vw;
      z-index: 200;
      width: 60px;
      height: 60px;
      margin-right: 0;
      background-size: contain;
    }

    &__menu-items {
      position: fixed;
      top: 0;
      left: 0;
      z-index: 200;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 100vw;
      max-width: initial;
      height: 100vh;
      // width: 100%;
      // height: 100%;
      background-color: $amethystSmoke;
    }

    &__menu-item:not(:last-child) {
      margin-bottom: 30px;
    }

    &__item-link {
      font-size: 38px;
      line-height: 48px;
      color: $black-text;
    }
  }
}
