.freelancer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100vh;
  margin-top: -90px;
  background-image: url("../images/FlBg.webp");
  background-repeat: no-repeat;
  background-position: right 22px;
  background-size: contain;

  // &__text-box {

  // }

  &__text {
    font-size: 24px;
    line-height: 32px;
  }

  &__bold-text {
    font-size: 60px;
    font-weight: 700;
    line-height: 72px;
  }

  &__btn {
    margin-top: 50px;
  }
}

@media screen and (max-width: 960px) {
  .freelancer {
    background-position: 45vw 22px;
  }
}

@media screen and (max-width: 800px) {
  .freelancer {
    background-position: 30vw 22px;
    background-size: cover;

    &__title {
      color: $black-text;
      text-shadow: 3px 3px 2px rgb(255, 253, 253);
    }
  }
}

@media screen and (max-width: 700px) {
  .freelancer {
    background-position: 20vw 40px;
  }
}

@media screen and (max-width: 450px) {
  .freelancer {
    background-position: 5vw 40px;
  }
}
