@import "reset";
@import "fonts";
@import "variables";
@import "burger";
// @import "../../node_modules/swiper/swiper-bundle.css";
@import "header";
@import "freelancer";
@import "services";
@import "portfolio";
@import "clients";
@import "testimonials";
@import "blog";
@import "contact";
@import "footer";

body {
  position: relative;
  font-family: "Red Hat Display", sans-serif;
  color: $black-text;
}

.container {
  width: 94%;
  max-width: 1110px;
}

.btn {
  display: inline-block;
  width: max-content;
  padding: 14px 48px;
  font-size: 24px;
  font-weight: 500;
  line-height: 32px;
  color: $white;
  text-align: center;
  background: $purple;
  border-radius: 100px;
  transition: 0.3s;
}

.btn:hover {
  box-shadow: 6px 5px 10px 5px rgba(131, 146, 157, 0.5), 6px 6px 5px -5px rgba(255, 255, 255, 0.6) inset;
  transition: 0.3s;
}

.section-name {
  position: relative;
  display: inline-block;
  width: max-content;
  margin-bottom: 27px;
  font-size: 18px;
  line-height: 24px;
  color: $purple;
}

.section-name::after {
  position: absolute;
  bottom: -10px;
  left: 0;
  display: inline-block;
  width: 75.75%;
  height: 2px;
  content: "";
  background-color: $purple;
}

.section-title {
  padding-bottom: 30px;
  font-size: 36px;
  font-weight: 700;
  line-height: 48px;
}

.blog__title {
  font-size: 30px;
}

.pharagraph {
  font-size: 16px;
  line-height: 32px;
  color: $gray-text;
}

.hidde {
  display: none;
}

.lock {
  overflow: hidden;
}

.swiper-pagination {
  display: none;
}

.swiper-pagination-bullet-active {
  background-color: $purple;
}

@media screen and (max-width: 1050px) {
  .swiper-pagination {
    position: absolute;
    bottom: 0;
    z-index: 100;
    display: block;
  }

  .swiper-pagination-bullet {
    width: 15px;
    height: 15px;
    margin: 0 7px;
  }

  .swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet {
    margin: 0 10px;
  }
}

@media screen and (max-width: 550px) {
  .swiper-pagination-bullet {
    width: 22px;
    height: 22px;
  }
}
