.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 30px 0;

  &__social-links {
    display: flex;
    justify-content: space-between;
    width: 190px;
  }

  &__link {
    display: flex;
    width: 50px;
    height: 50px;
  }

  &__link:nth-child(1) {
    background-image: url("../images/ball.webp");
  }

  &__link:nth-child(2) {
    background-image: url("../images/instagramm.webp");
  }

  &__link:nth-child(3) {
    background-image: url("../images/pinterest.webp");
  }
}
