.services {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px 0;

  &__wrapper {
    overflow: hidden;
  }

  &__items {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  &__item {
    display: flex;
    flex-direction: column;
    width: 32%;
  }

  &__item-head {
    display: flex;
    align-items: center;
    width: 100%;
    height: 62px;
    padding-left: 82px;
  }

  &__item:nth-child(3n+1) &__item-head {
    background: url("../images/services/services1.webp") no-repeat left;
  }

  &__item:nth-child(3n+2) &__item-head {
    background: url("../images/services/services2.webp") no-repeat left;
  }

  &__item:nth-child(3n+3) &__item-head {
    background: url("../images/services/services3.webp") no-repeat left;
  }

  &__title {
    font-size: 24px;
    font-weight: 500;
    line-height: 32px;
  }
}

@media screen and (max-width: 1050px) {
  .services {
    &__wrapper {
      position: relative;
    }

    &__items {
      padding-bottom: 60px;
    }

    &__item {
      flex-shrink: 0;
      align-items: center;
      text-align: center;
    }

    &__item-head {
      width: max-content;
    }
  }
}
