.burger {
  position: fixed;
  top: 20px;
  right: 4%;
  z-index: 200;
  display: none;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  cursor: pointer;
  background-color: $purple;
  border-radius: 50%;
}

.burger__icon {
  position: relative;
  width: 35px;
  height: 5px;
  background-color: $white;
}

.burger__icon::before {
  position: absolute;
  top: -13px;
  left: 0;
  width: 35px;
  height: 5px;
  content: '';
  background-color: $white;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
}

.burger__icon::after {
  position: absolute;
  top: 13px;
  left: 0;
  z-index: 30;
  width: 35px;
  height: 5px;
  content: '';
  background-color: $white;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
}

.burger__icon.burger__icon_active {
  background-color: transparent;
}

.burger__icon.burger__icon_active::before {
  top: 0;
  background-color: $white;
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
  transform: rotate(45deg);
}

.burger__icon.burger__icon_active::after {
  top: 0;
  background-color: $white;
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
  transform: rotate(-45deg);
}

@media screen and (max-width: 800px) {
  .burger {
    display: flex;
  }
}

.burger_disable {
  display: none;
}
